export function isUndefined(item: any): item is null | undefined {
  return !isDefined(item);
}

export function isDefined<T>(x: T): x is Exclude<T, null | undefined> {
  return x !== undefined && x !== null;
}

export function isNotEmpty(value: string | null | undefined): value is Exclude<string, null | undefined | ''> {
  return isNotNull(value) && value !== '';
}

export function isNotNull<T>(obj: T): obj is Exclude<T, null> {
  return isDefined(obj) && (obj !== null);
}

export function contains(sourceArray: Array<any>, element: any): boolean {
  return sourceArray.indexOf(element) !== -1;
}

export function isEmptyString(value: string | undefined): boolean {
  return isDefined(value) && value === '';
}
